import { FileType } from '../types.js';
import { createFileWithPath, fetchFile, getFilesFromZip, isZipFile, renameDuplicatedFiles, tableToHTML, toZip } from '../utils/index.js';
import { BaseFile } from './BaseFile.js';
import { FileWithPath } from './FileWithPath.js';
export class FileCollection {
    set name(name) {
        const nonExtName = name.replace(/\.[^/.]+$/, '');
        this._name = nonExtName;
    }
    get name() {
        return this._name;
    }
    get config() {
        return this._config;
    }
    set config(config) {
        this._config = {
            ...this._config,
            ...config
        };
    }
    async _addFromZip(file) {
        if (!this.name) {
            this.name = file.name;
        }
        const filesFromZip = await getFilesFromZip(file);
        this._files = [
            ...this._files,
            ...filesFromZip.map((f)=>new BaseFile(f)
            )
        ];
    }
    async _addFile(file) {
        const { name  } = file;
        if (file instanceof File) {
            file = createFileWithPath(file, {
                name
            });
        }
        if (isZipFile(name)) {
            return this._addFromZip(file);
        }
        this._files = [
            ...this._files,
            new BaseFile(file)
        ];
    }
    async _addFromUrl(fileUrl) {
        const name = fileUrl.split('/').pop() || '';
        const blob = await fetchFile(fileUrl);
        const file = createFileWithPath(blob, {
            name
        });
        return this._addFile(file);
    }
    async _addFromArray(files) {
        if (files.every((file)=>file instanceof File
        )) {
            for await (const file of files){
                await this._addFile(file);
            }
        }
    }
    setFiles(files) {
        this._files = files;
    }
    find(name) {
        const file1 = this._files.find((file)=>file.name === name
        );
        if (!file1) {
            throw new Error(`File ${name} not found`);
        }
        return file1;
    }
    filtered(files = this._files) {
        if (this._config.supportedFilesOnly) {
            return files.filter((file)=>file.isSupported()
            );
        }
        return files;
    }
    list(type) {
        let files = this._files;
        if (type === FileType.table) {
            files = this._files.filter((file)=>file.isTable()
            );
        } else if (type) {
            files = this._files.filter((file)=>file.name.endsWith(type)
            );
        }
        return this.filtered(files);
    }
    async toDsv(name) {
        return this.find(name).toDsvString();
    }
    async toJson(name) {
        return this.find(name).toJson();
    }
    async toTable(name) {
        return this.find(name).toTable();
    }
    async add(files) {
        if (!files) {
            throw new Error('No files provided');
        }
        if (files instanceof File || files instanceof FileWithPath) {
            return this._addFile(files);
        } else {
            if (!files.length) {
                throw new Error('No files provided');
            }
            if (Array.isArray(files)) {
                return this._addFromArray(files);
            }
            return this._addFromUrl(files);
        }
    }
    remove(options) {
        const { type , name  } = options;
        if (name) {
            this._files = this._files.filter((file)=>file.name !== name
            );
        } else if (type) {
            const files = this.list(type).map((file)=>file.name
            );
            this._files = this._files.filter((file)=>!files.includes(file.name)
            );
        }
    }
    clear() {
        this._files = [];
    }
    metadata(name) {
        return this.find(name).metadata();
    }
    rename(name, newName) {
        const file = this.find(name);
        file.rename(newName);
        this._files = [
            ...this._files
        ];
    }
    async toHtml(name) {
        const options = {
            style: {
                table: 'text-align: center; min-width: 100%; table-layout: fixed; border-collapse:collapse;',
                th: 'border: 1px solid; padding: 5px;',
                td: 'border: 1px solid; padding: 5px;'
            }
        };
        const html = await tableToHTML(this.find(name), options);
        return html;
    }
    async toZip(zipName = 'file-collection') {
        this._files = renameDuplicatedFiles(this._files);
        const dataURI = await toZip(this.filtered());
        const link = document.createElement('a');
        link.href = dataURI;
        link.download = `${zipName || this.name}.zip`;
        link.click();
    }
    copy() {
        const newFC = new FileCollection();
        newFC.name = this.name;
        newFC.setFiles([
            ...this._files
        ]);
        return newFC;
    }
    constructor(){
        this._files = [];
        this._name = '';
        this._config = {
            supportedFilesOnly: false
        };
    }
}
