import { BlobReader, BlobWriter, Data64URIWriter, ZipReader, ZipWriter } from '@zip.js/zip.js';
import { FileWithPath } from '../common/index.js';
export async function getFilesFromZip(zipFile) {
    const reader = new BlobReader(zipFile);
    const zipReader = new ZipReader(reader);
    /* eslint-disable @essex/adjacent-await */ const entries = await zipReader.getEntries();
    await zipReader.close();
    return Promise.all(entries.map((entry)=>getFileFromEntry(entry)
    ));
}
export async function getBlobFromEntry(entry) {
    const writer = new BlobWriter();
    const blob = entry.getData ? await entry.getData(writer) : new Blob();
    return blob;
}
export async function getFileFromEntry(entry) {
    const blob = await getBlobFromEntry(entry);
    return new FileWithPath(blob, entry.filename.split('/').pop() || '', entry.filename);
}
export async function toZip(files, writerOptions) {
    const zipWriter = new ZipWriter(new Data64URIWriter('application/zip'), writerOptions);
    for (const file of files){
        await zipWriter.add(file.name, new BlobReader(file));
    }
    const dataURI = await zipWriter.close();
    return dataURI;
}
